/* 
    Rules:
        1. Add suffix mentioning the usage
            eg: content-container
        
        2. Add __ to show sub-classes
            eg: content-container__title

        3. Variables should be in the following format
            --general-name-[type=color|background-color|size]
*/
/* Fonts */
@import url("https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    /* Body */
    --body-backgroud-color: black;
    --terminal-green: #8ae234;
    --terminal-blue: #729fce;
    --terminal-white: #ffffff;
    --terminal-grey: #666666;

    /* Generic colors */
    --saffron: #ff9933;
    --white: #ffffff;
    --green: #8ae234;
    --red: #d52a1f;
    --light-grey: #b4b4b4;

    /* UI Component colors */
    --link-color: #4ebcf7;
}

body {
    background-color: var(--body-backgroud-color);
    font-family: "Fira Code", monospace;
    overflow-x: hidden;
}

a {
    text-decoration: none;
}

::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-track {
    background: #181818;
}
::-webkit-scrollbar-thumb {
    background: var(--terminal-grey);
    border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
    background: #555;
}
